<template>
	<div class="vx-row">

		<div class="vx-col w-full mb-6">

			<h3 class="mb-3">{{ $t('statistics.pageTitle') }}</h3>
			<!-- Dropdown menu -->
			<vs-dropdown>

				<a class="flex items-center" href="#">
					{{ statisticType.label }}
					<i class="material-icons"> expand_more </i>
				</a>

				<vs-dropdown-menu>

					<vs-dropdown-item :key="index" v-for="type,index in statisticTypes" @click="changeStatisticType(type)">
						{{ type.label }}
					</vs-dropdown-item>

				</vs-dropdown-menu>
			</vs-dropdown>

		</div>

		<div class="vx-col w-full mb-6">

			<vx-card>

				<div class="vx-row">

					<div class="vx-col flex items-center">
						<!-- Datepicker component -->
						<datepicker placeholder="Select date" v-model="filter.date.start" format="dd.MM.yyyy"></datepicker>
					</div>

					<div class="vx-col flex items-center px-1">
						<i class="material-icons"> remove </i>
					</div>

					<div class="vx-col flex items-center">
						<!-- Datepicker component -->
						<datepicker placeholder="Select date" v-model="filter.date.end" format="dd.MM.yyyy"></datepicker>
					</div>

					<div class="vx-col flex flex-1 items-center" v-if="statisticType.slug === 'product'">

						<v-select v-model="filter.productSearch" label="title" :options="products" :reduce="product => product.id" :placeholder="$t('statistics.searchPlaceholder')" class="w-full" @search="searchProducts"></v-select>

					</div>

					<div class="vx-col">

						<vs-button @click="fetchData">{{ $t('actions.filter') }}</vs-button>

					</div>

				</div>

			</vx-card>

		</div>

		<!--Display statistic cards-->
		<div class="vx-col w-1/4 mb-3" :key="index" v-for="statistic,index in statisticData">
			<statistics-card-number
					:icon="statistic.icon"
					:statistic="statistic.value"
					:statisticTitle="getStatisticLabel(statistic.labelSlug)"
					v-if="!isHidden(statistic.onlyGeneral)"
			 />
		</div>

	</div>
</template>
<script>

    //Library for working with requests
    import axios from 'axios';

    //Application routes
    import { API } from "@/mixins/api/api";

    //Import icons
    import { DollarSignIcon, ShoppingBagIcon, ShoppingCartIcon, GiftIcon, PercentIcon } from 'vue-feather-icons';

    //Import a card component
    import VxCard from "../components/vx-card/VxCard";

    //Vue select
    import vSelect from 'vue-select';

    //Datepicker component
    import Datepicker from 'vuejs-datepicker';

    import StatisticsCardNumber from '@/components/statistics-cards/StatisticsCardNumber.vue'

    export default {
        props: {
            statisticTypes: {
                type: Array,
				default() {
                    return [
						{
						    label: this.$t('statistics.types.general'),
						    slug: 'general'
                        },
						{
						    label: this.$t('statistics.types.product'),
						    slug: 'product'
                        }
					]
				}
			}
		},
        components: {
            VxCard,
            Datepicker,
			StatisticsCardNumber,
			vSelect
        },
        data() {
            return {
                statisticType: this.statisticTypes[0],
				statisticData: {
                    grossSales: {
                        value: '300k',
                        labelSlug: 'grossSales',
						icon: 'DollarSignIcon',
						onlyGeneral: false
					},
					grossSalesDaily: {
                        value: '10k',
                        labelSlug: 'grossSalesDaily',
                        icon: 'DollarSignIcon',
                        onlyGeneral: false
					},
					netSales: {
                        value: '250k',
                        labelSlug: 'netSales',
                        icon: 'DollarSignIcon',
                        onlyGeneral: false
					},
					netSalesDaily: {
                        value: '7k',
                        labelSlug: 'netSalesDaily',
                        icon: 'DollarSignIcon',
                        onlyGeneral: false
					},
					ordersPlaced: {
                        value: '310',
                        labelSlug: 'ordersPlaced',
                        icon: 'ShoppingBagIcon',
                        onlyGeneral: false
					},
					itemsPurchased: {
                        value: '623',
                        labelSlug: 'itemsPurchased',
                        icon: 'ShoppingCartIcon',
                        onlyGeneral: false
					},
					couponsWorth: {
                        value: '20k',
                        labelSlug: 'couponsWorth',
                        icon: 'PercentIcon',
						onlyGeneral: true
					},
					giftCardsWorth: {
                        value: '30k',
                        labelSlug: 'giftCardsWorth',
                        icon: 'GiftIcon',
                        onlyGeneral: true
					}
				},
				filter: {
                    date: {
                        start: new Date(),
                        end: new Date()
					},
					productSearch: ''
				},
				products: [],
				API
            }
		},
        methods: {
			fetchData(){

			    let startDate = this.convertDate(this.dateFilter.start);
			    let endDate = this.convertDate(this.dateFilter.end);

			    let requestParams = {
			        params: {
			            start_date: startDate,
						end_date: endDate
					}
				};

			},
			getStatisticLabel(slug){

			    return this.$t('statistics.' + slug + '.label');

			},
			changeStatisticType(type){

			    this.statisticType = type;

			},
            searchProducts(search, loading){

                let requestParams = {

                    params: {
                        search: search
                    }

                };

                //Search products
                this.getProducts(requestParams);

            },
            getProducts(params) {


				this.products = this.loadItems('product', params);

            },
			isHidden(value){


			    if(this.statisticType !==  this.statisticTypes[0] && value){

			        return true;

				}

			}
		},
		mounted() {

		}
    }
</script>
